body {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: #6e6e73;
  font-size: 16px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}
input[type="number"]:hover,
input[type="number"]:focus {
  -moz-appearance: number-input;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.wrapper {
  min-height: 100vh;
  width: 83.3%;
  margin: 0 auto;
  max-width: 1220px;
  padding-top: 30px;
}
.basicButton {
  width: 100px;
  min-height: 40px;
  color: #6e6e73;
  border: none;
  border-top: 3px solid #ee4e69;
  background-color: #f6f6f6;
  box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.35);
  text-transform: uppercase;
  font-weight: 600;
}

.basicButton:hover {
  background-color: #ee4e69;
  color: #fff;
}

.select-container {
  position: relative;
}

.basicInput,
.basicSelect,
.select-label {
  display: block;
  border: none;
  border-radius: 5px;
  min-height: 40px;
  padding-left: 15px;
  box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.35);
}

.select-label {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  background-color: #fff;
  box-shadow: none;
}

.none {
  display: none;
}

/* FLATPICKR */
.flatpickr-confirm {
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
}

.flatpickr-confirm:hover {
  background: #eaeaea;
}

/* MAIN page start */

.datePickerWrapper {
  max-height: 305px;
  margin-right: 20px;
  background-color: #ef5350;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.35);
}

.dayContainer {
  box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.35);
}

.main-page .wrapper {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.main-info__btn-container {
  display: flex;
  margin: 0 auto;
  margin-bottom: 50px;
}

.main-info__new-order--btn,
.main-info__buy--btn {
  width: 300px;
  height: 60px;
}

.main-info__new-order--btn {
  margin-right: 50px;
}

.main-info__buy--btn {
  border-top: 3px solid #794eee;
}

.main-info__buy--btn:hover {
  background-color: #794eee;
}

.link {
  display: block;
  text-decoration: none;
}

/* MAIN page end */

/* newOrder start*/

.newOrder .wrapper {
  display: flex;
  width: 90%;
}

.newOrder__btn-container {
  display: flex;
  justify-content: space-between;
  width: 300px;
}

.consumer-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  margin: 0 auto;
}

.consumer-form .consumer-form__input {
  width: 300px;
  margin-bottom: 20px;
}

.menu-list__wrapper {
  width: 80%;
  margin-right: 15px;
}

.newOrder__toMain-link {
  display: block;
  margin-bottom: 30px;
}

.menu-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.menu-list__element {
  width: 24%;
  margin-bottom: 30px;
}

.menu-list__element-name {
  display: block;
  text-align: center;
  margin-bottom: 15px;
}

.menu-list__img {
  width: 100%;
  height: auto;
  min-height: 153px;
  margin-bottom: 15px;
}

.menu-list__saveBtn {
  width: 200px;
  height: 50px;
  display: block;
  margin: 0 auto;
}

.counterBnt {
  max-width: 50px;
  height: 50px;
  border: 3px solid #ee4e69;
  border-radius: 50%;
  box-shadow: none;
}

.counter {
  display: flex;
  justify-content: center;
}

.counter input {
  width: 50px;
  font-size: 18px;
  padding: 0;
  text-align: center;
  box-shadow: none;
}

/* notification */

.notification-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.35);
  display: flex;
  justify-content: center;
  align-items: center;
}

.notification {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 150px;
  border-radius: 7px;
  background-color: #fff;
  padding: 0 10px;
  box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.35);
}

.notification-text {
  color: #6e6e73;
  font-weight: 600;
  margin-bottom: 40px;
}

.notification-btn__container {
  display: flex;
  justify-content: center;
  width: 90%;
}

.notification-btn__container button {
  border: 2px solid #6e6e73;
  border-radius: 5px;
  box-shadow: none;
}

/* ORDER LIST */

.orderList {
  width: 20%;
  padding: 70px 5px 0 10px;
}

.orderList h2 {
  text-align: center;
}

.orderList__order-info {
  margin-bottom: 20px;
}

.orderList__order-info p {
  margin-bottom: 5px;
  font-weight: 600;
}

.order-info__time,
.order-info__name {
  text-align: center;
}

.order-info__price {
  font-weight: 600;
}

.orderList-dishes-list {
  margin-bottom: 30px;
}

.orderListElement {
  margin-bottom: 10px;
}

.orderListElement__name {
  display: inline-block;
  min-width: 85px;
}

.orderListElement__mount {
  min-width: 42px;
}

/* ORDER-LIST END */

/* ONE ORDER START */

.order-wrapper {
  width: 100%;
}

.consumer-header,
.oneOrder__menu-header,
.menu-info__list {
  display: flex;
  align-items: center;
  border-top: 3px solid #ee4e69;
  box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.35);
  min-height: 40px;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 20px;
}

.ConsumerInfoEl {
  display: inline-block;
  padding: 0 30px;
  border-right: 2px solid #6e6e73;
}

.ConsumerInfoEl:last-child {
  border-right: none;
}

.menu-header__category,
.menu-info__category {
  display: block;
  width: 12.5%;
  height: 38px;
  border-right: 2px solid #bfbfbf;
  text-align: center;
  line-height: 40px;
}

.menu-header__category--dish,
.menu-info__category--dish {
  flex-grow: 1;
}

.menu-header__category:last-child,
.menu-info__category:last-child {
  border-right: none;
}

.menu-info__list {
  position: relative;
  flex-direction: column;
  margin-bottom: 120px;
}

.menu-info__el {
  display: flex;
  width: 100%;
  border-bottom: 2px solid #bfbfbf;
}

.menu-info__el:last-child {
  border-bottom: none;
}

.total-price {
  position: absolute;
  bottom: -40px;
  right: 0;
  display: flex;
  width: 25%;
  height: 40px;
  border-top: 2px solid #bfbfbf;
  background-color: #fff;
  box-shadow: 1px 6px 10px 4px rgba(0, 0, 0, 0.35);
}

.total-price p {
  display: block;
  width: 50%;
  height: 40px;
  text-align: center;
  line-height: 35px;
  border-right: 2px solid #bfbfbf;
}

.total-price p:last-child {
  border-right: none;
}
/* ONE ORDER END */

/* INGR START */

.ingr-dates,
.ingr-header,
.ingr__list {
  width: 100%;
  border-top: 3px solid #794eee;
}

.ingr-dates {
  display: flex;
  justify-content: center;
}

/* INGR END */

